@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
*{box-sizing:border-box}html{font-family:"Roboto", sans-serif}body{margin:0;background-color:#f0eeef}.app{max-width:1060px;margin:0 auto;padding:0 30px}.app__title{margin:25px 0;text-align:center;font-size:40px}.app__info{margin-bottom:25px;font-size:12px}.app__text{margin-bottom:5px}.app__text:last-child{margin-bottom:0}

.post-list{display:flex;flex-direction:column;grid-gap:20px;gap:20px}

.post{padding:30px 50px;background-color:#f0eeef;border:1px solid rgba(255,255,255,0.2);border-radius:10px;box-shadow:-6px -6px 16px rgba(255,255,255,0.5),6px 6px 16px rgba(209,205,199,0.5);font-size:15px}.post__title{font-size:20px;line-height:24px;margin-bottom:10px}.post__info{line-height:19px;margin-bottom:20px}

.user{font-size:12px;opacity:0.7}

.comment-list{display:grid;grid-gap:20px;gap:20px}

.comment{padding:20px;border-radius:10px;background-color:rgba(255,255,255,0.5);box-shadow:-6px -6px 16px rgba(255,255,255,0.5),6px 6px 16px rgba(231,227,219,0.5);transition:transform 0.3s}.comment__user{font-size:12px;line-height:19px;opacity:0.7}.comment__text{margin-top:10px}.comment:hover{transform:scale(1.02)}

